* {
  margin: 0;
  padding: 0;
}

ol, ul, li {
  list-style: none;
}

a {
  text-decoration: none;
  color: #666;
}

.on {
  display: block !important;
}

.bg {
  color: red;
}

.basic, .special, .effect, .text {
  cursor: pointer;
  background: url(../images/bg_heritage_select.png) no-repeat 100% 50%;
}

.select {
  position: fixed;
  top: 0;
  left: 0;
  border-right: 1px solid #666;
  width: 285px;
  height: 100%;
  padding-left: 40px;
  z-index: 100;
}

.select div {
  margin-top: 5%;
  color: #666;
  font-size: 20px;
}

.select li {
  margin-top: 7%;
  padding-left: 5%;
  font-size: 18px;
  cursor: pointer;
}

.select__tit {
  font-size: 30px !important;
}

.sample-box {
  padding-left: 325px;
}

.main-tit {
  padding-top: 21%;
  text-align: center;
  font-size: 80px;
  color: #666;
  animation: trackingIn 1s forwards;
  animation-delay: !important;
  opacity: 0;
  font-weight: 800;
  display: none;
}

@keyframes trackingIn {
  0% {
    letter-spacing: -0.5em;
    transform: translate(0, 0);
    opacity: 0;
  }
  100% {
    transform: translate(0, 0);
    opacity: 1;
  }
}

.fadeIn {
  position: relative;
  text-align: center;
  padding-top: 10%;
  display: none;
}

.fadeIn img {
  animation: fadein 1.5s forwards;
  animation-delay: 0.5s !important;
  opacity: 0;
}

@keyframes fadein {
  0% {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    opacity: 0;
  }
  100% {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}

.fadeIn .fadeIn__tit {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  margin: auto;
}

.fadeIn .fadeIn__tit img {
  animation: fadein 1.5s forwards;
  animation-delay: 1s !important;
  opacity: 0;
}

@keyframes fadein {
  0% {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    opacity: 0;
  }
  100% {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}

